.catalog {
  padding-top: 37px;
  position: relative;
}

.catalog__title {
  margin: 0 0 26px 0;
  text-transform: uppercase;
  text-align: center;
}

.catalog__text {
  margin: 0 0 68px 0;
  text-transform: uppercase;
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
  color: #333333;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
